import { JSX } from 'preact';
import { useModal } from './hooks/useModal';
import { Layer } from '../UNSAFE_Layer';
import ModalBackdrop from './ModalBackdrop';
import ModalContainer from './ModalContainer';
import { cssProps } from '@oracle/oraclejet-cssprops';

type IntrinsicProps = Pick<JSX.HTMLAttributes<HTMLDivElement>, 'children'>;
type UseModalProps = Parameters<typeof useModal>[0];
type Props = UseModalProps & IntrinsicProps;

const styles = {
  base: cssProps`${{
    position: 'fixed',
    top: '0',
    right: '0',
    bottom: '0',
    left: '0'
  }}`
};

const ModalWrapper = ({ children }: IntrinsicProps) => <div class={styles.base}>{children}</div>;

const Modal = ({ children, isOpen, onBackdropClick }: Props) => {
  const { modalRef } = useModal({ isOpen, onBackdropClick });

  return isOpen ? (
    <Layer>
      <ModalWrapper>
        <ModalBackdrop />
        <ModalContainer ref={modalRef}>{children}</ModalContainer>
      </ModalWrapper>
    </Layer>
  ) : null;
};

export default Modal;
